<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process248"></a>

<p><br></p>
<p><span class="rvts17">薄情寡义（</span><span class="rvts50">Thinning the Herd</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:113,Y:256</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：瘦黄男人（</span><span class="rvts20">Sallow Man</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">与神王立誓，杀掉所有队友</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1375.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">该任务首先需要站在黑环势力一边，对抗亚历山大的魔导师大军。在成功劝说瘦黄男人后，并把亚历山大的头颅带给他。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1376.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">作为奖励，他会给你一把可以</span><span class="rvts20"></span><span class="rvts21">碎尸</span><span class="rvts20"></span><span class="rvts21">的法杖。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1377.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">来到神学院，大门处有一个虚空怪把守，使用费恩与它对话可以得知，该虚空虫是一个变形者，他还是费恩以前的同事。他会劝说费恩效忠他们种族的</span><span class="rvts20"></span><span class="rvts21">神王</span><span class="rvts20"></span><span class="rvts21">，并怂恿恩杀掉其余队友。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1378.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">费恩可以选择杀掉其它队友，之后使用</span><span class="rvts20"></span><span class="rvts21">碎尸</span><span class="rvts20"></span><span class="rvts21">法杖，使所有队友不可复活。这样做，将影响整个游戏的最终结局。之后，宣誓神王却不杀掉队友或不按照神王的旨意行事，会招惹神王的愤怒。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



